<template>
  <v-dialog :value="value" width="600px" persistent>
    <v-card>
      <v-card-title class="text-h5">  </v-card-title>
      <v-card-text>
        <v-alert
          text
          type="warning"
          icon="mdi-alert-circle"
          dismissible
          close-icon="mdi-close"
          v-model="warningAlert"
          >{{ indications.warning }}</v-alert
        >
        <v-alert
          text
          type="error"
          icon="mdi-alert"
          dismissible
          close-icon="mdi-close"
          v-model="errorAlert"
          >{{ indications.error }}</v-alert
        >
        <v-form ref="form">
          <v-row>
            <v-col cols="12">
              <v-text-field
                label="*"
                type="password"
                required
                v-model="password"
                :rules="[(value) => !!value || '']"
              ></v-text-field>
            </v-col>
            <v-col cols="12">
              <v-text-field
                type="password"
                label="*"
                required
                v-model="passwordAgain"
                validate-on-blur
                :rules="[
                  (value) => !!value || '',
                  (value) => value === password || ''
                ]"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn @click="$emit('input', false)">  </v-btn>
        <v-btn color="primary" @click="reset" :loading="reseting">  </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import { resetPassword } from "../../../api/user";
export default {
  props: ["value", "user"],
  data() {
    return {
      // indications
      indications: {
        warning: "",
        error: ""
      },
      warningAlert: false,
      errorAlert: false,
      password: "",
      passwordAgain: "",
      reseting: false
    };
  },
  watch: {
    value(val) {
      if (!val) {
        // 
        this.password = "";
        this.passwordAgain = "";
        this.$refs.form.resetValidation();
      }
    }
  },
  methods: {
    async reset() {
      const valid = this.$refs.form.validate();
      if (valid) {
        this.reseting = true;
        await resetPassword(this.user.id, this.password);
        this.$notify.success("");
        this.reseting = false;
        this.$emit("input", false);
      }
    }
  }
};
</script>
