<template>
  <v-dialog :value="value" width="400px" persistent>
    <v-card>
      <v-card-title class="text-h5">  </v-card-title>
      <v-card-text class="my-4">
        <v-icon color="warning" class="mr-2">mdi-alert-circle</v-icon>
         {{ users.length }} 
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn @click="$emit('input', false)">  </v-btn>
        <v-btn color="primary" @click="deleteUsers" :loading="deleting">
          
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import { deleteUsers } from "../../../api/user";

export default {
  props: ["value", "users"],
  data() {
    return {
      deleting: false
    };
  },
  methods: {
    async deleteUsers() {
      this.deleting = true;
      await deleteUsers(this.users.map((u) => u.id));
      this.deleting = false;
      this.$emit("input", false);
      this.$notify.success(`${this.users.length} `);
      setTimeout(() => {
        this.$notify.info("..", true);
        this.$emit("reload");
      }, 800);
    }
  }
};
</script>
